.start {
  justify-content: center;
  align-items: center;
  display: flex;
  margin-bottom: 20px;
}
.hero {
  width: 100%;
  position: relative;
}
.hero img {
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.headline {
  position: absolute;
  top: -20%;
  left: 15%;
  font-size: 100px;
  transform: translate(-10%, 70%);
  color: white;
  z-index: 2;
}
.subTitle{
  position: absolute;
  top: 100px;
  left: 400px;
  font-size: 40px;
  color: white;
  z-index: 2;
}

.wave {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100px;
  background-color: rgba(131, 62, 242, 0.25);
  background: url(../assets/wave2.png);
  background-size: 1000px 100px;
}
.wave.wave1 {
  animation: animate 30s linear infinite;
  z-index: 100;
  opacity: 0.2;
  animation-delay: 0s;
  bottom: 0;
}
.wave.wave2 {
  animation: animate2 15s linear infinite;
  z-index: 99;
  opacity: 0.3;
  animation-delay: -2s;
  bottom: 0px;
}

@keyframes animate {
  0% {
    background-position-x: 0;
  }

  100%{
    background-position-x: 1000px;
  }
}
@keyframes animate2 {
  0% {
    background-position-x: 0;
  }

  100%{
    background-position-x: -1000px;
  }
}
